.search-bar{
  margin: 20px;
  text-align: center;
}

.search-bar input{
  width: 75%;
  border: 1px solid #ddd;
  border-radius: 10px;
}

.video-item img{
  max-width: 64px;
}

.video-detail .details{
  margin-top: 10px;
  padding: 10px;
  border: 1px solid #ddd;
  border-radius: 4px;
}

.list-group-item{
  margin-left: 15px;
  cursor: pointer;
}

.list-group-item:hover{
  background-color: #eee;
}



